import apiClient from '../api/axios';
import { mockPatients, mockPatientDetails, Patient, PatientDetail } from '../mocks/mockPatients';

// Simulate network delay for mock mode
const simulateDelay = (min: number = 300, max: number = 800): Promise<void> => {
  const delay = Math.random() * (max - min) + min;
  return new Promise(resolve => setTimeout(resolve, delay));
};

class PatientService {
  private useMocks: boolean;

  constructor() {
    this.useMocks = process.env.REACT_APP_USE_MOCKS === 'true';
    console.log(`[PatientService] Initialized with useMocks: ${this.useMocks}`);
  }

  /**
   * Get all patients
   */
  async getPatients(): Promise<Patient[]> {
    if (this.useMocks) {
      console.log('[PatientService] Using mock data for getPatients');
      await simulateDelay();
      return mockPatients;
    } else {
      console.log('[PatientService] Fetching patients from real API');
      try {
        const response = await apiClient.get<Patient[]>('/patients');
        console.log('[PatientService] Successfully fetched patients from real API:', response.data.length, 'patients');
        return response.data;
      } catch (error) {
        console.warn('[RealMode] API call failed - getPatients:', error);
        console.warn('[RealMode] Falling back to mock data for getPatients');
        return mockPatients;
      }
    }
  }

  /**
   * Get patient details by ID
   */
  async getPatientDetail(patientId: number): Promise<PatientDetail> {
    if (this.useMocks) {
      console.log(`[PatientService] Using mock data for getPatientDetail(${patientId})`);
      await simulateDelay();
      const patient = mockPatientDetails[patientId];
      if (!patient) {
        throw new Error(`Patient with ID ${patientId} not found`);
      }
      return patient;
    } else {
      console.log(`[PatientService] Fetching patient details from real API for ID: ${patientId}`);
      try {
        const response = await apiClient.get<PatientDetail>(`/patients/${patientId}`);
        console.log(`[PatientService] Successfully fetched patient ${patientId} from real API:`, response.data.name);
        return response.data;
      } catch (error) {
        console.warn(`[RealMode] API call failed - getPatientDetail ${patientId}:`, error);
        console.warn(`[RealMode] Falling back to mock data for patient ${patientId}`);
        const patient = mockPatientDetails[patientId];
        if (!patient) {
          throw new Error(`Patient with ID ${patientId} not found in mock data`);
        }
        return patient;
      }
    }
  }

  /**
   * Assign RN to patient
   */
  async assignRN(patientId: number): Promise<any> {
    if (this.useMocks) {
      console.log(`[PatientService] Mock assignRN for patient ${patientId}`);
      await simulateDelay();
      const patient = mockPatients.find(p => p.id === patientId);
      return {
        success: true,
        message: `${patient?.name || 'Patient'} assigned to RN`,
        patientId,
      };
    } else {
      console.log(`[PatientService] Assigning RN via real API for patient ${patientId}`);
      try {
        const response = await apiClient.post(`/patients/${patientId}/assign-rn`);
        console.log(`[PatientService] Successfully assigned RN to patient ${patientId} via real API:`, response.data);
        return response.data;
      } catch (error) {
        console.warn(`[RealMode] API call failed - assignRN ${patientId}:`, error);
        console.warn(`[RealMode] Falling back to mock assignment for patient ${patientId}`);
        const patient = mockPatients.find(p => p.id === patientId);
        return {
          success: true,
          message: `${patient?.name || 'Patient'} assigned to RN (mock fallback)`,
          patientId,
        };
      }
    }
  }

  /**
   * Escalate patient to MD
   */
  async escalatePatient(patientId: number): Promise<any> {
    if (this.useMocks) {
      console.log(`[PatientService] Mock escalatePatient for patient ${patientId}`);
      await simulateDelay();
      const patient = mockPatients.find(p => p.id === patientId);
      return {
        success: true,
        message: `${patient?.name || 'Patient'} escalated to MD`,
        patientId,
      };
    } else {
      console.log(`[PatientService] Escalating patient via real API for patient ${patientId}`);
      try {
        const response = await apiClient.post(`/patients/${patientId}/escalate`);
        console.log(`[PatientService] Successfully escalated patient ${patientId} via real API:`, response.data);
        return response.data;
      } catch (error) {
        console.warn(`[RealMode] API call failed - escalatePatient ${patientId}:`, error);
        console.warn(`[RealMode] Falling back to mock escalation for patient ${patientId}`);
        const patient = mockPatients.find(p => p.id === patientId);
        return {
          success: true,
          message: `${patient?.name || 'Patient'} escalated to MD (mock fallback)`,
          patientId,
        };
      }
    }
  }
}

// Export singleton instance
export const patientService = new PatientService();
export default patientService;
